.dashboard {
    margin-bottom: 105px;
}

.dashboard-item {
    padding: 1px 25px;
    border-radius: 16px;
    background-color: #363636;
    box-shadow: 2px 2px 20px #0e0e0e;

    h1 {
        text-shadow: 1px 1px 8px black;
        font-size: 1.5em;
    }

    &.guide p {
        margin: 10px 0;
    }
}